import React, { Component } from 'react'
import "./Employment.css"

class Employment extends Component {

    render() {

        let utreview_logo = require("./../img/utreview_logo.png")
        let equinor_logo = require("./../img/equinor_logo.png")

        return (
            <main id="about-main" className="">

                <div className="d-inline">

                    <div className="content-panel content-panel-bg">

                        <div>
                            <h2 className="heading-title">Employment</h2>
                        </div>

                        <div className="col-6 d-inline-block">

                            <div id="about-text d-inline">
                                <img src={utreview_logo} className="logo-img" />
                                <h5>UT Review</h5>
                                <p>
                                    Co-Founder, Frontend/Backend Developer
                                </p>
                                <p><i>May 2020 - Present</i></p>
                            </div>
                        </div>

                        <div className="col-6 d-inline-block">
                            <div id="about-text d-inline">
                                <img src={equinor_logo} className="logo-img" />
                                <h5>Equinor</h5>
                                <p>
                                    IT and Data Management Intern
                                </p>
                                <p><i>June 2020 - July 2020</i></p>
                            </div>

                        </div>

                    </div>
                </div>
            </main>
        )
    }
}

export default Employment